<template>
  <div>
    <tab-page ref="tabPage">
      <template slot="messageAmountDay">
        <data-table :data-table="dataTable" ref="dataTable">
          <div slot="conditions" style="margin-bottom:15px">
            <el-form :inline="true" :model="queryParams">
              <el-form-item>
                <el-date-picker v-model="queryParams.beginTime" type="date" placeholder="选择日期时间" size="small" style="width:170px"></el-date-picker>
              </el-form-item>
              <span class="span-style">至</span>
              <el-form-item>
                <el-date-picker v-model="queryParams.endTime" type="date" placeholder="选择日期时间" size="small" style="width:170px"></el-date-picker>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSubmit" size="small" style="margin-top:0">查询</el-button>
              </el-form-item>
            </el-form>
          </div>
          <template slot="columns">
            <el-table-column prop="beginTime" label="日期" width="200"></el-table-column>
            <el-table-column prop="total" label="总条数" width="100"></el-table-column>
            <el-table-column prop="submitedCount" label="提交成功条数" width="120"></el-table-column>
            <el-table-column prop="successCount" label="状态报告成功条数" width="150"></el-table-column>
            <el-table-column prop="failCount" label="状态报告失败条数" width="150"></el-table-column>
            <el-table-column prop="noReportCount" label="未获取到状态报告条数" ></el-table-column>
            <el-table-column prop="successRate" label="成功率" width="100" :formatter="formatSuccessRate"></el-table-column>
            <el-table-column prop="getRate" label="获取率" width="100" :formatter="formatGetRate"></el-table-column>
          </template>
        </data-table>
      </template>
    </tab-page>
  </div>
</template>
<style scoped>
  .el-form-item__content .el-button {
    margin-top: 20px;
  }

  .el-table--border th {
    text-align: center;
  }

  .el-table th .cell {
    text-align: center;
  }

  .el-form--inline .el-form-item {
    margin-right: 2px;
  }

  .el-form-item {
    margin-bottom: 2px;
  }

  .el-dialog__footer {
    padding: 10px 0px;
    text-align: center;
  }

  .check-all-select {
    padding: 10px;
  }

  .check-all-select span.text-all {
    font-size: 14px;
    font-weight: bold;
  }

  .check-all-select label {
    float: right;
  }

  .mb-10 {
    margin-bottom: 10px;
  }
</style>
<script>
  import common from '../../../utils/common'
  import formatDate from '../../../utils/formatDate'
  import DataTable from "../../../components/DataTable"
  import TabPage from "../../../components/TabPage"
  export default{
    components: {
      DataTable,
      TabPage,
    },
    data(){
        return{
          initBeginTime:'',
          initEndTime:'',
          queryParams:{
            beginTime: '',//开始时间
            endTime: ''//结束时间
          },
          dataTable: {
            pageSize: 1000,
            pageSizes: [10, 20, 50, 100, 1000],
            url: "/web/v1/newsms/statisticbyday",
            queryParams:{
                beginTime:'',
                endTime:''
            },
            emptyText: '没有记录',
          },
        }

    },
    mounted(){
      var defaultPage = {label: "短信发送量查询(日)", name: "messageAmountDay", slotName: 'messageAmountDay', closable: false};
      this.$refs.tabPage.$emit("open-tab", defaultPage);
      var myDate = new Date();
      this.initEndTime = this.dealDate(myDate.getTime());
      this.initBeginTime = this.dealDate(myDate.getTime()-30*24*60*60*1000);
      this.dataTable.queryParams.beginTime =  this.initBeginTime;
      this.dataTable.queryParams.endTime =  this.initEndTime;
      this.queryParams.beginTime =  this.initBeginTime;
      this.queryParams.endTime =  this.initEndTime;
    },
    methods: {
      onSubmit: function () {
        this.dataTable.queryParams = this.queryParams;
        var tag = this.warnTime(this.dataTable.queryParams.beginTime, this.dataTable.queryParams.endTime);
        if (tag) {
          return;
        }
        this.dataTable.queryParams.beginTime = this.dealDate(this.dataTable.queryParams.beginTime);
        this.dataTable.queryParams.endTime = this.dealDate(this.dataTable.queryParams.endTime);
        this.dataTable.url = '/web/v1/newsms/statisticbyday';
        this.$refs.dataTable.$emit('load-data');

      },
      warnTime(startTime, endTime){
        if (!startTime) {
          this.$notify({
            message: '查询开始时间不能为空！',
            type: 'warning'
          });
          return true;
        }
        if (!endTime) {
          this.$notify({
            message: '查询结束时间不能为空！',
            type: 'warning'
          });
          return true;
        }
        let timeStart = new Date(startTime).getTime();
        let timeEnd = new Date(endTime).getTime();
        if (timeStart > timeEnd) {
          this.$notify({
//                        title: '警告',
            message: '查询开始时间不能大于结束时间',
            type: 'warning'
          });
          return true;
        }
        return false;
      },
      dealTime(time){
        return formatDate(new Date(time), 'yyyy-MM-dd hh:mm:ss');
      },
      dealDate(date){
        return formatDate(new Date(date), 'yyyy-MM-dd');
      },
      formatterDate(row, column){
        return formatDate(row.date, 'yyyy-MM-dd');
      },
      formatJson(filterVal, jsonData) {
        return jsonData.map(v => filterVal.map(j => {
            let returnValue;
            if (j == 'operator') {
              returnValue = v[j].name;
            } else {
              returnValue = v[j];
            }
            return returnValue;
          }))
      },
      formatSuccessRate(row){
        let result;
        if(row['successRate'] == null) {
          return '--';
        }else{
          return parseFloat(row['successRate']*100).toFixed(2)+'%';
        }
      },
      formatGetRate(row){
        if(row['getRate'] == null) {
          return '--';
        }else{
          return parseFloat(row['getRate']*100).toFixed(2)+'%';
        }
      }

    }
  }
</script>
<style>
  .span-style {
    display: inline-block;
    margin-top: 8px;
    font-size: 14px;
  }
</style>
